.container .home-page-container nav .nav-bar-container .nav-btn-container h3 {
  color: #f1f1f1;
  font-weight: 400;
  position: relative;
  cursor: pointer;
}
.container .home-page-container nav .nav-bar-container .nav-btn-container h3::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  bottom: -5px;
  background-color: #c1dfff;
  transform: scaleX(0);
  transition: transform 0.35s ease;
}
.container .home-page-container nav .nav-bar-container .nav-btn-container h3:hover::after {
  transform: scaleX(1);
}
.container .home-page-container nav .nav-bar-container .mobile-menu-btn {
  position: relative;
  width: 30px;
  height: 3px;
  background-color: #f1f1f1;
  display: none;
  transition: all 0.5s ease-in-out;
}
.container .home-page-container nav .nav-bar-container .mobile-menu-btn::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 3px;
  background-color: #f1f1f1;
  left: 0;
  top: -7px;
  transition: all 0.5s ease-in-out;
}
.container .home-page-container nav .nav-bar-container .mobile-menu-btn::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 3px;
